.sidebar {
  width: 180px; /* Reduced from 250px */
  height: 100vh;
  background-color: #1f2937;
  color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar-header {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
  color: #3b82f6;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-menu li {
  margin-bottom: 1rem;
}

.sidebar-link {
  text-decoration: none;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 500;
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  transition: background-color 0.3s ease;
}

.sidebar-link:hover {
  background-color: #3b82f6;
  color: #ffffff;
}

.content-container {
  margin-left: 180px; /* Adjusted to match the new sidebar width */
  width: calc(100% - 180px); /* Adjust width accordingly */
}
